html, body, #root {
  margin: 0; padding: 0; height: 100%; width: 100%;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, sans-serif;
  background: #000;           /* fallback colour */
  overflow-x: hidden;
}

/* Make sure the React root can float ABOVE the aurora */
#root { position: relative; z-index: 0; }

/* ---- Aurora Sky ---- */
body::before {
  content: '';
  position: fixed;
  top: -50vh; left: -50vw;
  width: 200vw; height: 200vh;

  background:
    radial-gradient(circle at 30% 30%, #52faff 0%, transparent 50%),
    radial-gradient(circle at 70% 40%, #32ff72 0%, transparent 50%),
    radial-gradient(circle at 50% 70%, #ff66ff 0%, transparent 50%);
  mix-blend-mode: screen;
  opacity: 0.38;
  animation: auroraMove 28s ease-in-out infinite alternate;
  will-change: transform;
  pointer-events: none;
  z-index: -1;                /* <= keeps it behind everything */
}

@keyframes auroraMove {
  0%   { transform: translate(-15%,  0%)   scale(1.1) rotate(0deg);  }
  50%  { transform: translate( 10%, 15%)  scale(1.2) rotate(12deg); }
  100% { transform: translate(-10%, -15%) scale(1.1) rotate(-8deg); }
}

/* ----- Dropâ€‘zone & buttons ----- */
.dropzone {
  border: 2px dashed #999; border-radius: 16px;
  min-height: 260px;
  display: flex; flex-direction: column; align-items: center; justify-content: center;
  background: rgba(255,255,255,0.04);
  color: #ccc; cursor: pointer; transition: border .2s;
}
.dropzone:hover { border-color: #61dafb; color: #fafafa; }

.primary-btn, .secondary-btn {
  padding: 10px 18px;
  border: none; border-radius: 8px;
  font-size: 1rem; font-weight: 600;
  cursor: pointer;
}
.primary-btn   { background: #61dafb; color: #000; }
.secondary-btn { background: #444;    color: #fff; }
.primary-btn:disabled { opacity: 0.6; cursor: not-allowed; }

/* Responsive spacing */
@media (max-width: 600px) {
  main             { padding: 1.5rem 0.5rem; }
  .primary-btn,
  .secondary-btn   { width: 100%; }
}
